import React from 'react';
import styled from 'styled-components';
import {injectIntl} from "react-intl";

const Modal = injectIntl(({handleCloseClick, handleSubmit, handleChangeCompany, handleChangeYear, handleChangeWebsite, handleChangeName, handleChangePosition, intl}) => {
    const two = intl.formatMessage({id:"two"});
    const twoText1 = intl.formatMessage({id:"twoText1"});
    const twoForm1 = intl.formatMessage({id:"twoForm1"});
    const twoForm2 = intl.formatMessage({id:"twoForm2"});
    const twoForm3 = intl.formatMessage({id:"twoForm3"});
    const twoForm4 = intl.formatMessage({id:"twoForm4"});
    const twoForm5 = intl.formatMessage({id:"twoForm5"});
    const twoFormBtn = intl.formatMessage({id:"twoFormBtn"});
    return(
        <ModalWrapped>
            <div className="modalFormContainer">
                <span className="closeButton" onClick={handleCloseClick}>x</span>
                <form onSubmit={handleSubmit}>
                    <div className="modalFormWrapped">                
                        <div className="modalFormHeader">
                            <span className="modalFormTitle red">{two}</span>
                            <br/>
                            <span className="modalFormSubtitle">{twoText1}</span>
                        </div>
                        <div className="modalForm">                        
                            <input type="text" onChange={handleChangeCompany} name="companyName" placeholder={twoForm1}/>
                            <input type="number" onChange={handleChangeYear} name="year" placeholder={twoForm2}/>
                            <input type="text" onChange={handleChangeWebsite} name="website" placeholder={twoForm3}/>
                            <input type="text" onChange={handleChangeName} name="name" placeholder={twoForm4}/>
                            <input type="text" onChange={handleChangePosition} name="position" placeholder={twoForm5}/>
                        </div>
                        <div className="modalFormBtn">
                            <input type="submit" value={twoFormBtn} />
                        </div>                
                    </div>
                </form>
            </div>
        </ModalWrapped>
    )
});

export default Modal;

const ModalWrapped = styled.div`
    position: fixed;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0,0,0,0.5);
    z-index: 5;
    font-family: 'Gotham Condensed', sans-serif;
    color: #4E4E4E;

    .closeButton{
        font-family: 'Bebas Neue',sans-serif;
        position: absolute;
        top: -15px;
        right: -15px;
        display: block;
        cursor: pointer;
        font-size: 25px;
        background-color: #E20613;
        border-radius: 50%;
        width: 30px;
        height: 30px;
        text-align: center;
        color: #ffffff;
        line-height: 35px;    
    }

    .closeButton:hover{
        color: #E20613;
        background-color: #ffffff;
    }

    .modalFormContainer{
        width: 300px;
        position: absolute;
        max-height: 530px;
        min-height: 420px;
        height: 100%;
        background-color: #ffffff;
        top: 0px;
        right: 0px;
        bottom: 0px;
        left: 0px;
        margin: auto;
        display: grid;
        grid-template-columns: 230px;
        grid-template-rows: 400px;
        justify-content:center;
        align-content: center;
    }

    .modalFormWrapped{
        width: 230px;
        height: 435px;
        display: grid;
        grid-template-rows: 60px 245px 60px;
        grid-template-columns: 100%;
        grid-gap: 35px;
    }

    .modalFormHeader{
        text-align: center;
    }

    .modalFormTitle{
        font-family: 'Bebas Neue',sans-serif;
        font-size: 30px;
    }

    .modalFormSubtitle{
        font-size: 22px;
    }

    .modalForm{
        display:grid;
        grid-template-rows: 44px 44px 44px 44px;
        grid-template-columns: 100%;
        grid-gap: 11px;
    }

    input[type="text"],
    input[type="number"] {
        background-color: #f4f5f7;
        border: none;
        width: calc(100% - 15px);
        padding-left: 15px;
        font-size: 18px;
        height: 44px;
    }

    input[type="submit"] {
        width: 100%;
        border: none;
        background-color: #E20613;
        height: 60px;
        line-height: 60px;
        font-size: 25px;
        color: #ffffff;
        cursor:pointer;
    }
`;



