import React from 'react';
import styled from 'styled-components';

const Headers = ({titleBlue, titleRed, subtitle, idTag}) => (
    <HeadersWrapper className={subtitle?'subtitle':null}>    
        <div id={idTag} className="title">
            {titleBlue} 
            {undefined !== titleRed ? <span className="red">{titleRed}</span> : null}
        </div>
    </HeadersWrapper>
);

export default Headers;

const HeadersWrapper = styled.div` 
    padding-top: 45px;
    padding-bottom: 45px;
    text-align: center;

    .subtitle{
        padding-bottom: 20px !important;
    }

    .title{
        color: #004586;
        font-family: 'Bebas Neue', sans-serif;
        font-size: 50px;
        text-transform: uppercase;
    }

    .red{
        color: #E20613;
    }

    

    @media (min-width: 700px) {
        .title{
            font-size: 70px;
        }
    }
`;





