import React from 'react';
import styled from 'styled-components';
import ImageDescription from '../components/imageDescription';
import Header from '../components/headers';
import Elegibility from '../images/seccion2/foto_elegibility.jpg';
import Great from '../images/seccion2/foto_great.jpg';
import {injectIntl} from "react-intl";

const Section2 = injectIntl(({intl}) => {
    const whyParticipateRed = intl.formatMessage({id:"whyParticipateRed"});
    const whyParticipateBlue = intl.formatMessage({id:"whyParticipateBlue"});
    const whyParticipateText1 = intl.formatMessage({id:"whyParticipateText1"});
    const whyParticipateText2 = intl.formatMessage({id:"whyParticipateText2"});
    const aGreatOpportunity = intl.formatMessage({id:"aGreatOpportunity"});
    const aGreatOpportunityText1 = intl.formatMessage({id:"aGreatOpportunityText1"});
    const aGreatOpportunityText2 = intl.formatMessage({id:"aGreatOpportunityText2"});
    const aGreatOpportunityText3 = intl.formatMessage({id:"aGreatOpportunityText3"});
    const elegibility = intl.formatMessage({id:"elegibility"});
    const elegibilityText1 = intl.formatMessage({id:"elegibilityText1"});
    const elegibilityText2 = intl.formatMessage({id:"elegibilityText2"});
    const elegibilityText3 = intl.formatMessage({id:"elegibilityText3"});
    const elegibilityText4 = intl.formatMessage({id:"elegibilityText4"});
    const elegibilityText5 = intl.formatMessage({id:"elegibilityText5"});
    return(
        <Section2Wrapper>
            <Header 
                titleBlue={whyParticipateBlue} 
                titleRed={whyParticipateRed} 
                idTag="whyparticipate" 
                subtitle={true}
            />
            <div className="subtitle">{whyParticipateText1}<br/>
            {whyParticipateText2}</div>
            <div className="imageDesc">
                <div className="iDContainer">
                    <div className="iDTextContainer">
                        <span className='red title'>{aGreatOpportunity}</span><br/><br/>
                        {aGreatOpportunityText1}<br/><br/>
                        {aGreatOpportunityText2}<br/><br/>
                        {aGreatOpportunityText3}<br/><br/>
                    </div>
                    <ImageDescription image={Great} isArrowLeft={true}/>
                </div>
            </div>
            <div className="imageDesc">
                <div className="iDContainer">
                    <ImageDescription image={Elegibility} isArrowLeft={false}/>
                    <div className="iDTextContainer">
                        <span className='red title'>{elegibility}</span><br/><br/>
                        {elegibilityText1}<br/><br/>
                        {elegibilityText2}<br/>
                        {elegibilityText3}<br/>
                        {elegibilityText4}<br/>
                        {elegibilityText5}<br/>
                    </div>
                </div>
            </div>
            
            
        </Section2Wrapper>
    )
});

export default Section2;

const Section2Wrapper = styled.div` 
    .imageDesc{
        margin-bottom: 50px;
    }
    .subtitle{
        font-family: 'Gotham Condensed', sans-serif;
        color: #4E4E4E;
        font-size: 25px;
        padding-top: 20px;
        text-align: center;
        max-width: 680px;
        margin: auto;
        padding-bottom: 45px;
    }
    
`;





