import React from 'react';
import styled from 'styled-components';
import AboutFFImg from '../images/about/foto_about_fff.jpg';
import AboutSiSImg from '../images/about/foto_about_sis.jpg';
import arrow from '../images/general/triangle_image.png';
import FbIcon from '../images/about/facebook.svg';
import InstaIcon from '../images/about/instagram.svg';
import LnIcon from '../images/about/linkedin.svg';
import TwIcon from '../images/about/twitter.svg';
import WebIcon from '../images/about/web.svg';
import YtIcon from '../images/about/youtube.svg';
import {injectIntl} from "react-intl";

const About = injectIntl(({intl}) => {
    const fffSocialNetwork = intl.formatMessage({id:"fffSocialNetwork"});
    const fffAdress = intl.formatMessage({id:"fffAdress"});
    const sisSocialNetwork = intl.formatMessage({id:"sisSocialNetwork"});
    return(
        <AboutWrapper>
            <div id="about" className="container">
                <div className="textContainer">
                    <span className='blue big-text title'>{fffSocialNetwork}</span><br/><br/>
                    <span>{fffAdress}</span>
                    <div className="socialNetworks">
                        <div className="socialNetwork">
                            <a href="https://www.fff.fr/" target="_blank" rel="noopener noreferrer">
                                <img className="heighResponsive" src={WebIcon} alt="web"/>
                            </a>
                        </div>
                        <div className="socialNetwork">
                            <a href="https://www.facebook.com/equipedefrance?fref=ts" target="_blank" rel="noopener noreferrer">
                                <img className="heighResponsive" src={FbIcon} alt="facebook"/>
                            </a>
                        </div>
                        <div className="socialNetwork">
                            <a href="https://twitter.com/equipedefrance" target="_blank" rel="noopener noreferrer">
                                <img className="heighResponsive" src={TwIcon} alt="twitter"/>
                            </a>
                        </div>
                        <div className="socialNetwork">
                            <a href="https://www.instagram.com/equipedefrance/" target="_blank" rel="noopener noreferrer">
                                <img className="heighResponsive" src={InstaIcon} alt="instagram"/>
                            </a>
                        </div>
                        <div className="socialNetwork">
                            <a href="https://www.linkedin.com/company/federation-fran-aise-de-football/" target="_blank" rel="noopener noreferrer">
                                <img className="heighResponsive" src={LnIcon} alt="linkedin"/>
                            </a>
                        </div>
                        <div className="socialNetwork">
                            <a href="https://www.youtube.com/user/LesBleusTV" target="_blank" rel="noopener noreferrer">
                                <img className="heighResponsive" src={YtIcon} alt="youtube"/>
                            </a>
                        </div>
                    </div>
                </div>
                <div className="imageContainer">
                    <img className="imageDes" src={AboutFFImg} alt="x" />
                    <img className="arrowPos left" src={arrow} alt="Arrow" />
                </div>  
            </div>
            <div className="separation"/>
            <div className="container">            
                <div className="imageContainer">
                    <img className="arrowPos right" src={arrow} alt="Arrow" />
                    <img className="imageDes" src={AboutSiSImg} alt="x" />
                </div>
                <div className="textContainer">
                    <span className='title red big-text'>{sisSocialNetwork}</span>
                    <div className="socialNetworks">
                        <div className="socialNetwork">
                            <a href="http://sis.news" target="_blank" rel="noopener noreferrer">
                                <img className="heighResponsive" src={WebIcon} alt="web"/>
                            </a>
                        </div>
                        <div className="socialNetwork">
                            <a href="https://www.facebook.com/SISnews/" target="_blank" rel="noopener noreferrer">
                                <img className="heighResponsive" src={FbIcon} alt="facebook"/>
                            </a>
                        </div>
                        <div className="socialNetwork">
                            <a href="https://twitter.com/sis" target="_blank" rel="noopener noreferrer">
                                <img className="heighResponsive" src={TwIcon} alt="twitter"/>
                            </a>
                        </div>
                    </div>
                </div>
            </div>
        </AboutWrapper>
    )
});

export default About;

const AboutWrapper = styled.div` 
    margin: 100px auto;

    .separation{
        margin: 25px;   
    }

    .container{
        width: 90%;
        margin: auto;
    }   

    .textContainer{
        background-color: #ffffff;
        padding: 15px 30px;
        font-family: 'Gotham Condensed', sans-serif;
        font-size: 18px;
        color: #4E4E4E;
    }

    .title{
        text-transform: uppercase;
        font-size: 22px;
    }

    .red{
        color: #E20613;
    }

    .imageContainer{
        position: relative;
        overflow: hidden;
        background-color: #000000;
    }

    .imageDes{
        max-width: 100%;
        height: auto;
        margin: auto;
    }

    .arrowPos{
        display: none;
    }

    .imageOver{
        max-width: 100%;
        max-height: 80%;
        margin: auto;
        position: absolute;
        top: 0px;
        left: 0px;
        right: 0px;
        bottom: 0px;
    }

    .socialNetworks {
        display: grid;
        grid-template-rows: 30px;
        grid-template-columns: 10% 10% 10% 10% 10% 10%;
        grid-gap: 8%;
        width: 90%;
        margin: 30px auto 0px;
    }

    .heighResponsive{
        height: 100%;
        width: 100%;
    }

    @media (min-width: 700px) {
        .container{
            width: 680px;
            display: grid;
            grid-template-columns: 50% 50%;
            margin: auto;
        }   
        .imageContainer{
            position: relative;
            overflow: hidden;
        }

        .imageDes{
            max-height: 100%;
            width: auto;
            position: absolute;
            left: -50%;
            right: -50%;
            max-width: none;
        }

        .arrowPos{
            display: block;
            position: absolute;
            top:0px;
            bottom: 0px;
            margin: auto;
        }

        .left{
            left: 0px;
        }

        .right{
            right: 0px;
            -webkit-transform: scaleX(-1);
            transform: scaleX(-1);
        }
    }

     @media (min-width: 1000px) {
        .container{
            width: 905px;
        }
        .textContainer{
            height: 225px;
        }
     }
`;





