import React from 'react';
import styled from 'styled-components';
import LogoFFF from '../images/footer/logo_fff.svg';
import LogoSiS from '../images/footer/logo_sis_blanco.png';
import {injectIntl} from "react-intl";

const Footer = injectIntl(({intl}) => {
    const footerMenu01 = intl.formatMessage({id:"footerMenu01"});
    const footerMenu02 = intl.formatMessage({id:"footerMenu02"});
    const footerMenu03 = intl.formatMessage({id:"footerMenu03"});
    const footerMenu04 = intl.formatMessage({id:"footerMenu04"});
    const footerMenu05 = intl.formatMessage({id:"footerMenu05"});
    const footerMenu06 = intl.formatMessage({id:"footerMenu06"});
    //const footerMenu07 = intl.formatMessage({id:"footerMenu07"});
    //const footerMenu08 = intl.formatMessage({id:"footerMenu08"});
    const footerMenu09 = intl.formatMessage({id:"footerMenu09"});
    const footerMenu10 = intl.formatMessage({id:"footerMenu10"});
    const footerMenu11 = intl.formatMessage({id:"footerMenu11"});
    const footerMenu12 = intl.formatMessage({id:"footerMenu12"});
    return(
        <FooterWrapper>
            <div className="logoContainer">
                <img className="imgResponsive" src={LogoFFF} alt="FFF" />
            </div>
            <div className="container">
                <div className="linkSite">
                    <div><a href="#presentation">{footerMenu01}</a></div>
                    <div><a href="#categories">{footerMenu02}</a></div>
                    <div><a href="#whyparticipate">{footerMenu03}</a></div>
                    <div><a href="#apply">{footerMenu04}</a></div>
                    <div><a href="#deadline">{footerMenu05}</a></div>
                    <div><a href="#prizes">{footerMenu06}</a></div>
                    
                    <div><a href="#about">{footerMenu09}</a></div>
                    <div><a href="#ticketing">{footerMenu10}</a></div>                
                </div>
                <div className="contact">
                    <div className="mail">
                        <div className="mailTitle">{footerMenu11}</div>
                        <div className="mailText"><a href="mailto:fffinnovationawards@fff.fr">fffinnovationawards@fff.fr</a></div>
                    </div>
                    <div className="bottomLine">
                        <a href="http://sis.news/seccion/evento?lang=en#section7" target="_blank" rel="noopener noreferrer" ><div className="newsletterBtn">{footerMenu12}</div></a>
                        <div className="bottomLogoContainer">
                            <img src={LogoSiS} alt="SiS" />
                        </div>
                    </div>

                </div>
            </div>
        </FooterWrapper>
    )
});

export default Footer;

const FooterWrapper = styled.div`
    position: relative;
    background-color: #004586;
    padding-top: 80px;
    color: #ffffff;
    font-family: 'Gotham Condensed', sans-serif;

    .logoContainer{
        width: 109px;
        height: 117px;
        position: absolute;
        left: 0px;
        right: 0px;
        top: -65px;
        margin: auto;
    }

    .container{
        width: 90%;
        margin: auto;
        display: grid;
        grid-template-columns: 100%; 
        grid-template-rows: 180px 200px;
        grid-gap: 30px 10px;
    }

    .linkSite{
        display: grid;
        grid-template-columns: 45% 45%; 
        grid-template-rows: 20px 20px 20px 20px 20px;
        grid-gap: 10%;
    }

    .contact{
        display: grid;
        grid-template-columns: 100%;
        grid-template-rows: 40px 90px;
        grid-gap: 10px 10px;
    }

    .mailTitle{font-family: 'Bebas Neue', sans-serif;}

    .newsletterBtn{
        font-family: 'Bebas Neue', sans-serif;
        width: 80%;
        margin: 0px auto 30px;
        background-color: #B79257;
        color: #ffffff;
        padding: 15px 0px;
        cursor: pointer;
        text-align: center;
    }

    .mailText a{
        color: #ffffff;
        text-decoration: none;
    }

    .bottomLogoContainer{
        width:114px;
        height: 52px;
        margin: auto;
    }

    @media (min-width: 700px) {
        .container{
            width: 680px;
            grid-template-columns: 45% 45%;
            grid-template-rows: 155px;
            grid-gap: 10%;
        }

        .linkSite{
            display: grid;
            grid-template-columns: 30% 30% 30%; 
            grid-template-rows: 20px 20px 20px 20px;
            grid-gap: 5%;
        }

        .bottomLine{
            display: grid;
            grid-template-columns: 60% 35%;
            grid-gap: 5%;
        }

        .newsletterBtn{
            width: 100%;
            margin: auto;
        }
    }

    @media (min-width: 1000px) {
        .container{
            width: 980px;
        }
    }
`;





