import React from 'react';
import styled from 'styled-components';
import {injectIntl} from "react-intl";

const Cover = injectIntl(({backgroundImage, whiteText, redText, redBig, intl}) => {
    const title = intl.formatMessage({id:whiteText});
    const subTitle = intl.formatMessage({id:redText});

    return(<CoverWrapper>
        <div className="oppacityLayer" />
        <img className='imgResponsive coverImg' src={backgroundImage} alt={title}/>
        <div className='coverText'>
            <div className={'coverTextContainer ' + (redBig?'redBig':'')}>
                <div className='whiteText'>{title}</div>
                <div className={'redText ' + (redBig?'big':'')}>{subTitle}</div>
            </div>
        </div>
    </CoverWrapper>)
});

export default Cover;

const CoverWrapper = styled.div` 
    position: relative;

    .oppacityLayer{
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0px;
        background-color: rgba(0,0,0,0.3);
    }

    .coverImg{
        width: 100%;
    }

    .coverText{
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0px;
        display: grid;
        justify-content:center;
        align-content: center;
        display: grid;
        grid-template-columns: auto;
        grid-template-rows: auto;
    }

    .coverTextContainer {
        line-height: 35px;
        font-size: 40px;
    }

    .redBig{
        line-height: 52px;
    }

    .coverText div{
        text-align: center;
        text-transform: uppercase;
        font-family: 'Bebas Neue', sans-serif;        
    }

    .whiteText{
        color: #ffffff;        
    }

    .redText{
        color: #E20613;        
    }

    .big{
        font-size: 40px;
    }

    @media (min-width: 700px) {
        .coverTextContainer {
            line-height: 75px;
            font-size: 90px;
        }

        .big{
            font-size: 90px;
        }

        .redBig{
            line-height: 125px;
        }
    }

    @media (min-width: 1190px) {
        .coverTextContainer {
            line-height: 145px;
            font-size: 180px;
        }

        .big{
            font-size: 180px;
        }

        .redBig{
            line-height: 210px;
        }
    }
`;





