import React from 'react';
import styled from 'styled-components';
import arrow from '../images/general/triangle_image.png';

const ImageDescription = ({image, overImg, isArrowLeft}) => (
    <ImageDescriptionWrapper>    
        
            <img className="imageDes" src={image} alt="FFF-SiS" />
            {undefined!==overImg?<img className="imageOver" src={overImg} alt="Over" />:null}
            <img className={"arrowPos " + (isArrowLeft?"left":"right")} src={arrow} alt="Arrow" />
        
    </ImageDescriptionWrapper>
);

export default ImageDescription;

const ImageDescriptionWrapper = styled.div` 
    position: relative;
    overflow: hidden;
    background-color: #000000;

    .imageDes{
        max-width: 100%;
        height: auto;
        margin: auto;
    }

    .arrowPos{
        display: none;
    }

    .imageOver{
        max-width: 100%;
        max-height: 80%;
        margin: auto;
        position: absolute;
        top: 0px;
        left: 0px;
        right: 0px;
        bottom: 0px;
    }

    .imageContainer{
        position: relative;
        overflow: hidden;
        background-color: #000000;
    }

    @media (min-width: 700px) {
        .imageContainer{
            position: relative;
            overflow: hidden;
        }
        
        .imageDes{
            max-height: 100%;
            width: auto;
            position: absolute;
            left: -50%;
            right: -50%;
            max-width: none;
        }

        .arrowPos{
            display: block;
            position: absolute;
            top:0px;
            bottom: 0px;
            margin: auto;
        }

        .left{
            left: 0px;
        }

        .right{
            right: 0px;
            -webkit-transform: scaleX(-1);
            transform: scaleX(-1);
        }
    }

    
`;





