import React, { Component } from 'react';
import { IntlProvider } from 'react-intl';
import axios from 'axios';
import AppLocale from './languageProvider';
import StartImage from './images/cover/imagen_inicio.jpg';
import DeadlineImage from './images/cover/fondo_deadline.jpg';
import Header from './sections/header';
import Cover from './sections/cover';
import Section1 from './sections/section1';
import Section2 from './sections/section2';
import Apply from './sections/apply';
import Prices from './sections/prices';
import About from './sections/about';
import Footer from './sections/footer';
import Modal from './components/modal';

class App extends Component {
  state = {
    modalVisible: false,
    currentAppLocale: AppLocale['en'],
    company: null,
    year: 0,
    website: null,
    name: null,
    position: null
  }

  handleModalToggle = (evt) => {
    this.setState({
      modalVisible: !this.state.modalVisible
    });
  }

  setEnglish = (evt) => {
    this.setState({
      currentAppLocale: AppLocale['en'],
    });
  }

  setFrench = (evt) => {
    this.setState({
      currentAppLocale: AppLocale['fr'],
    });
  }

  handleChangeCompany = event => {
    this.setState({ company: event.target.value });
  }
  handleChangeYear = event => {
    this.setState({ year: event.target.value });
  }
  handleChangeWebsite = event => {
    this.setState({ website: event.target.value });
  }
  handleChangeName = event => {
    this.setState({ name: event.target.value });
  }
  handleChangePosition = event => {
    this.setState({ position: event.target.value });
  }
  

  handleSubmit = event => {
    event.preventDefault();

    if(null !== this.state.name && 
      null !== this.state.job &&
      null !== this.state.company &&
      null !== this.state.video ){
      const user = {
        company: this.state.company,
        year: this.state.year,
        website: this.state.website,
        name: this.state.name,
        position: this.state.position
      };

      axios.post(`./CreateUser.php`,  user)
        .then(res => {
          console.log(res);
        }).catch((error) => {
          console.log(error);
      });
    }
  }

  render() {
    return (
      <IntlProvider
                    locale={this.state.currentAppLocale.locale}
                    messages={this.state.currentAppLocale.messages} >
        <div className="landing">
          {
              this.state.modalVisible &&
              <Modal 
              handleCloseClick={this.handleModalToggle}
              handleSubmit={this.handleSubmit}
              handleChangeCompany={this.handleChangeCompany}
              handleChangeYear={this.handleChangeYear}
              handleChangeWebsite={this.handleChangeWebsite}
              handleChangeName={this.handleChangeName}
              handleChangePosition={this.handleChangePosition}
            />
          }        
          <Header setEnglish={this.setEnglish} setFrench={this.setFrench} />
          <Cover backgroundImage={StartImage} whiteText={'coverTitle'} redText={"coverSubtitle"} redBig={true}/>
          <Section1 />
          <Section2 />
          <Apply  handleClick={this.handleModalToggle}/>
          <div id="deadline" />
          <Cover backgroundImage={DeadlineImage} whiteText={'deadline'} redText={"deadlineDate"} redBig={false}/>
          <Prices  />
          <About  />
          <Footer />
        </div>
      </IntlProvider>
    );
  }
}

export default App;



