import React from 'react';
import styled from 'styled-components';
import Headers from '../components/headers';
import tree from '../images/apply/icono_aplly3.png';
import {injectIntl} from "react-intl";

const Apply = injectIntl(({intl, handleClick}) => {
    const applyNowBlue = intl.formatMessage({id:"applyNowBlue"});
    const applyNowRed = intl.formatMessage({id:"applyNowRed"});
    const one = intl.formatMessage({id:"one"});
    const oneText1 = intl.formatMessage({id:"oneText1"});
    const oneText2 = intl.formatMessage({id:"oneText2"});
    const oneText3 = intl.formatMessage({id:"oneText3"});
    const oneText4 = intl.formatMessage({id:"oneText4"}); 
    const oneText5 = intl.formatMessage({id:"oneText5"});
    const oneText6 = intl.formatMessage({id:"oneText6"});
    const two = intl.formatMessage({id:"two"});
    const twoText1 = intl.formatMessage({id:"twoText1"});
    const twoText2 = intl.formatMessage({id:"twoText2"});
    const three = intl.formatMessage({id:"three"});
    const threeText = intl.formatMessage({id:"threeText"});
    const four = intl.formatMessage({id:"four"});
    const fourText = intl.formatMessage({id:"fourText"});
    const five = intl.formatMessage({id:"five"});
    const fiveText = intl.formatMessage({id:"fiveText"});
    const six = intl.formatMessage({id:"six"});
    const sixText = intl.formatMessage({id:"sixText"});
    return(
        <ApplyWrapper>    
            <Headers titleBlue={applyNowBlue} titleRed={applyNowRed} idTag="apply" />
            <div className="applyCards">
                
                    <div className="applyCard">
                        <div className="applyCardTitle blue">{one}</div>
                        <div className="applyCardBody">
                            <div className="applyCardDesc">
                                {oneText1}<a href="mailto:innovationawards@fff.fr"  className="blue">innovationawards@fff.fr</a><br/>
                                {oneText2}<br/>
                                {oneText3}<br/>
                                {oneText4}<br/>
                                {oneText5}<br/>
                                {oneText6}
                            </div>
                        </div>
                    </div>
                    <div className="applyCard">
                        <div className="applyCardTitle red">{two}</div>
                        <div className="applyCardBody">
                            <div className="applyCardDesc">
                                {twoText1}
                            </div>
                            <button className="applyCardBtn" onClick={handleClick}>{twoText2}</button>
                        </div>
                    </div>
                    <div className="applyCard">
                        <div className="applyCardTitle blue">{three}</div>
                        <div className="applyCardBody">
                            <div className="applyCardDesc">
                                {threeText}
                            </div>
                            <div className="applyCarImg"><img src={tree} alt="Finalist"/></div>
                        </div>
                    </div>
                    <div className="applyCard">
                        <div className="applyCardTitle red">{four}</div>
                        <div className="applyCardBody">
                            <div className="applyCardDesc">
                                {fourText}
                            </div>
                        </div>
                    </div>
                    <div className="applyCard">
                        <div className="applyCardTitle blue">{five}</div>
                        <div className="applyCardBody">
                            <div className="applyCardDesc">
                                {fiveText}
                            </div>
                        </div>
                    </div>
                    <div className="applyCard">
                        <div className="applyCardTitle red">{six}</div>
                        <div className="applyCardBody">
                            <div className="applyCardDesc">
                                {sixText}
                            </div>
                        </div>
                    </div>
                
            </div>
        </ApplyWrapper>
    )
});

export default Apply;

const ApplyWrapper = styled.div` 
    margin-bottom: 80px;

    .applyCards{
        width: 90%;
        margin: auto;
        text-align: center;
    }

    .applyCard{
        background-color: #ffffff;
        padding: 24px;
        margin: 0px 0px 15px;
    }

    .applyCardTitle{
        font-family: 'Bebas Neue', sans-serif;
        font-size: 24px;
        margin-bottom: 20px;
    }

    .applyCardBody{
        font-family: 'Gotham Condensed', sans-serif;
        color: #4E4E4E;
        font-size: 18px;
    }

    .applyCardBtn{
        width: 80%;
        margin: 40px auto 15px;
        background-color: #B79257;
        color: #ffffff;
        padding: 15px 0px;
        cursor: pointer;
    }
    .applyCarImg{
        max-width: 100%;
        height: auto;
        margin: 40px auto 15px;
    }

    @media (min-width: 700px) {
        .applyCards{            
            width: 680px;
            display: grid;
            grid-template-columns: 30% 30% 30%;
            grid-template-rows: 530px 235px;
            grid-gap: 40px 5%;
        }
        .applyCardBtn{
            margin: 140px auto 0px;
        }
        .applyCarImg{
            margin: 100px auto 0px;
        }
        .applyCard{
            margin: 0px;
        }
    }

    @media (min-width: 1000px) {
        .applyCards{
            width: 905px;
            grid-template-columns: 30% 30% 30%;
            grid-template-rows: 420px 200px;
        }
        .applyCardBtn{
            margin: 100px auto 0px;
        }
        .applyCarImg{
            margin: 70px auto 0px;
        }
    }
`;





