import React from 'react';
import styled from 'styled-components';
import ImageDescription from '../components/imageDescription';
import Header from '../components/headers';
import { Card, CardImg, CardText, CardBody,
    CardTitle } from 'reactstrap';
import imagePresentation from '../images/seccion1/presentation.jpg';
import imageCard1 from '../images/seccion1/foto_categoria1.jpg';
import imageCard2 from '../images/seccion1/foto_categoria2.jpg';
import imageCard3 from '../images/seccion1/foto_categoria3.jpg';
import {injectIntl} from "react-intl";
  

const Section1 = injectIntl(({intl}) => {
    const purpose = intl.formatMessage({id:"purpose"});
    const purposeText = intl.formatMessage({id:"purposeText"});
    const aboutSis = intl.formatMessage({id:"aboutSis"});
    const aboutSisText = intl.formatMessage({id:"aboutSisText"});
    const categories = intl.formatMessage({id:"categories"});
    const sportPerformanceBlue = intl.formatMessage({id:"sportPerformanceBlue"});
    const sportPerformanceRed = intl.formatMessage({id:"sportPerformanceRed"});
    const sportPerformanceText = intl.formatMessage({id:"sportPerformanceText"});
    const protectingPlayerBlue = intl.formatMessage({id:"protectingPlayerBlue"});
    const protectingPlayerRed = intl.formatMessage({id:"protectingPlayerRed"});
    const protectingPlayerText = intl.formatMessage({id:"protectingPlayerText"});
    const fanEngagementBlue = intl.formatMessage({id:"fanEngagementBlue"});
    const fanEngagementRed = intl.formatMessage({id:"fanEngagementRed"});
    const fanEngagementText = intl.formatMessage({id:"fanEngagementText"});
    return(
        <Section1Wrapper>
            <Header titleBlue="1. Presentation" idTag="presentation"/>
            <div className="iDContainer">
                <div className="iDTextContainer">
                    <span className='red iDTitle'>{purpose}</span><br/>
                    {purposeText}<br/><br/>
                    <span className='red iDTitle'>{aboutSis}</span><br/>
                    {aboutSisText}
                </div>
                <ImageDescription image={imagePresentation} isArrowLeft={true}/>
            </div>  
            <Header titleBlue={categories} idTag="categories" />
            <div className="cards">
                <Card>
                    <CardBody>
                        <CardTitle>{sportPerformanceBlue} <span className="red">{sportPerformanceRed}</span></CardTitle>
                    </CardBody>
                    <CardImg top width="100%" src={imageCard1} alt="Sport Performance" />
                    <CardBody>
                        <CardText>
                            {sportPerformanceText}
                        </CardText>
                    </CardBody>
                </Card>
                <Card>
                    <CardBody>
                        <CardTitle>{protectingPlayerBlue} <span className="red">{protectingPlayerRed}</span></CardTitle>
                    </CardBody>
                    <CardImg top width="100%" src={imageCard3} alt="Sport Performance" />
                    <CardBody>
                        <CardText>
                            {protectingPlayerText}
                        </CardText>
                    </CardBody>
                </Card>
                <Card>
                    <CardBody>
                        <CardTitle>{fanEngagementBlue} <span className="red">{fanEngagementRed}</span></CardTitle>
                    </CardBody>
                    <CardImg top width="100%" src={imageCard2} alt="Sport Performance" />
                    <CardBody>
                        <CardText>
                            {fanEngagementText}
                        </CardText>
                    </CardBody>
                </Card>
            </div>
        </Section1Wrapper>
    )
});

export default Section1;

const Section1Wrapper = styled.div` 
    .cards{
        width: 90%;
        margin: auto;     
    }

    .card{
       background-color: #ffffff;
       text-align: center;
       margin-bottom: 15px;
    }

    .card-title{
        color: #004586;
        font-family: 'Bebas Neue', sans-serif;
        font-size: 28px;
    }

    .card-title {
    margin: 35px auto 0px;
}

    .card-body{
        font-family: 'Gotham Condensed', sans-serif;
        font-size: 18px;
        color: #4E4E4E;
        padding: 0px 12px 30px;
    }

    @media (min-width: 700px) {
        .cards{
            width: 680px;  
            display: grid;
            grid-template-columns: 30% 30% 30%;
            grid-gap: 5%;                
        }
    }

     @media (min-width: 1000px) {
        .cards{
            width: 905px;
        }
     }
`;





