import React from 'react';
import styled from 'styled-components';
import ImageDescription from '../components/imageDescription';
import PrizesImg from '../images/prizes/prizes.jpg';
import TicketsImg from '../images/prizes/icono_ticketing.png';
import VenueImg from '../images/prizes/icono_venue.png';
import {injectIntl} from "react-intl";

const Prizes = injectIntl(({intl}) => {
    const prizes = intl.formatMessage({id:"prizes"});
    const prizesText1 = intl.formatMessage({id:"prizesText1"});
    const prizesText2 = intl.formatMessage({id:"prizesText2"});
    const prizesText3 = intl.formatMessage({id:"prizesText3"});
    const prizesText4 = intl.formatMessage({id:"prizesText4"});
    const prizesText5 = intl.formatMessage({id:"prizesText5"});
    const ticketingText1 = intl.formatMessage({id:"ticketingText1"});
    const ticketingText2 = intl.formatMessage({id:"ticketingText2"});
    const venue = intl.formatMessage({id:"venue"});
    const venueText1 = intl.formatMessage({id:"venueText1"});
    return(
        <PrizesWrapper>    
            <div id="prizes"/>
            <div className="imageDesc">
                <div className="iDContainer">
                    <div className="iDTextContainer">
                        <span className='blue big-text'>{prizes}</span><br/><br/>
                        {prizesText1}<br/>    
                        {prizesText2}<br/>    
                        {prizesText3}<br/>    
                        {prizesText4}<br/>    
                        {prizesText5}<br/>      
                    </div>
                    <ImageDescription image={PrizesImg} isArrowLeft={true}/>
                </div>
            </div>
            <div id="ticketing" className="cards">
                <div className="card">
                    <div className="topCard blue-king-bg">
                        <img src={TicketsImg} alt="Tickets" />
                    </div>
                    <div className="cardBody">
                        <div className="cardBodyTitle blue-king">{ticketingText1}</div>
                        <a href="http://sis.news"><div className="cardBodyBtn">{ticketingText2}</div></a>
                    </div>
                </div>
                <div className="card">
                    <div className="topCard blue-light-bg">
                        <img src={VenueImg} alt="Venue" />
                    </div>
                    <div className="cardBody">
                        <div className="cardBodyTitle blue-light">{venue}</div>
                        <div className="cardBodyText">{venueText1}</div>
                    </div>
                </div>
            </div>
        </PrizesWrapper>
    )
});

export default Prizes;

const PrizesWrapper = styled.div` 
    margin: 80px auto;

    .cards{
        width: 90%;
        margin: 40px auto 0px;
    }
    .card{
        margin-bottom: 30px;
        background-color: #ffffff;
    }

    .topCard{
        width: 100%;
        padding: 15px 0px;
    }

    .topCard img{
        margin: auto;
        max-width: 100%;
        height: auto;
        display: block;
    }
    .cardBody{
        font-family: 'Bebas Neue', sans-serif;
        padding: 20px 30px;
        text-align: center;
        color: #4E4E4E;
    }
    .cardBodyTitle{
        font-size: 30px;
    }
    .cardBodyText{
        font-size: 18px;
        margin: 40px auto 15px;
    }
    .cardBodyBtn{
        width: 80%;
        margin: 40px auto 15px;
        background-color: #E20613;
        color: #ffffff;
        padding: 15px 0px;
        cursor: pointer;
    }
    
    @media (min-width: 700px) {
        .cards{
            width: 680px;
            display: grid;
            grid-template-columns: 45% 45%;
            grid-template-rows: 400px;
            grid-gap: 10%;
        }
        .card{
            margin-bottom: 0px;
            display:grid;
            grid-template-columns: 100%;
            grid-template-rows: 50% 50%;
        }
        .cardBody{
            padding: 20px 80px;
        }
        .cardBodyBtn, .cardBodyText{
            margin: 22px auto 0px;
        }
    }

    @media (min-width: 1000px) {
        .cards{
            width: 905px;
        }
    }
`;





