import Enlang from './entries/en_US';
import Frlang from './entries/fr';
import { addLocaleData } from 'react-intl';

const AppLocale = {
  en: Enlang,
  fr: Frlang
};
addLocaleData(AppLocale.en.data);
addLocaleData(AppLocale.fr.data);

export default AppLocale;



